(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('UserManagementController', UserManagementController)
        .directive('aiUserManagement', function () {
            return {
                restrict: 'E',
                replace: true,
                templateUrl: 'app/admin/components/user/userManagement.html',
                scope: {},
                bindToController: {
                    acbId: '@',
                    atlId: '@'
                },
                controllerAs: 'vm',
                controller: 'UserManagementController'
            };
        });

    /** @ngInject */
    function UserManagementController (commonService, $log, $uibModal) {
        var vm = this;

        vm.updateUser = updateUser;
        vm.inviteUser = inviteUser;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.roles = [];
            if (!vm.acbId && !vm.atlId) { // not managing acb or atl, managing entire CHPL
                vm.roles.push('ROLE_ADMIN');
                vm.roles.push('ROLE_CMS_STAFF');
                vm.roles.push('ROLE_ONC_STAFF');
            }
            if (!vm.atlId) { // not managing ATL; either managing entire CHPL or single ACB
                vm.roles.push('ROLE_ACB_ADMIN');
                vm.roles.push('ROLE_ACB_STAFF');
            }
            if (!vm.acbId) { // not managing ACB; either managing entire CHPL or single ATL
                vm.roles.push('ROLE_ATL_ADMIN');
                vm.roles.push('ROLE_ATL_STAFF');
            }
        }

        function updateUser (user) {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/user/userEdit.html',
                controller: 'EditUserController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    user: function () { return user; },
                    action: function () { return 'edit'; },
                    acbId: function () { return vm.acbId; },
                    atlId: function () { return vm.atlId; }
                }
            });
            vm.modalInstance.result.then(function () {
                vm.freshenUsers();
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug(result);
                }
            });
        }

        function inviteUser () {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/user/userEdit.html',
                controller: 'EditUserController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    user: function () { return {}; },
                    action: function () { return 'invite'; },
                    acbId: function () { return vm.acbId; },
                    atlId: function () { return vm.atlId; }
                }
            });
            vm.modalInstance.result.then(function (result) {
                $log.info(result);
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug(result);
                }
            });
        }

        vm.freshenUsers = function () {
            if (vm.acbId) {
                commonService.getUsersAtAcb(vm.acbId)
                    .then(function (response) {
                        vm.users = response.users;
                    }, function (error) {
                        $log.debug(error);
                    });
            } else if (vm.atlId) {
                commonService.getUsersAtAtl(vm.atlId)
                    .then(function (response) {
                        vm.users = response.users;
                    }, function (error) {
                        $log.debug(error);
                    });
            } else {
                commonService.getUsers()
                    .then(function (response) {
                        vm.users = response.users;
                    }, function (error) {
                        $log.debug(error);
                    });
            }
        };
        vm.freshenUsers();
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('EditUserController', EditUserController);

    /** @ngInject */
    function EditUserController ($uibModalInstance, user, action, acbId, atlId, commonService) {
        var vm = this;
        vm.user = angular.copy(user);
        vm.action = action;
        vm.acbId = acbId;
        vm.atlId = atlId;

        vm.save = save;
        vm.cancel = cancel;
        vm.deleteUser = deleteUser;
        vm.invite = invite;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.userInvitation = {permissions: []};
            vm.roles = [];
            vm.message = '';
            if (!vm.acbId && !vm.atlId) {
                vm.roles.push('ROLE_ADMIN');
                vm.roles.push('ROLE_CMS_STAFF');
                vm.roles.push('ROLE_ONC_STAFF');
            }
            if (!vm.atlId) {
                vm.roles.push('ROLE_ACB_ADMIN');
                vm.roles.push('ROLE_ACB_STAFF');
            }
            if (!vm.acbId) {
                vm.roles.push('ROLE_ATL_ADMIN');
                vm.roles.push('ROLE_ATL_STAFF');
            }
        }

        function save () {
            if (!vm.user.roles)
                vm.user.roles = [];

            var roleObject = {subjectName: vm.user.user.subjectName};
            for (var i = 0; i < vm.roles.length; i++) {
                var payload = angular.copy(roleObject);
                payload.role = vm.roles[i];
                if (vm.user.roles.indexOf(payload.role) > -1) {
                    commonService.addRole(payload);
                } else if (!vm.acbId && !vm.atlId) {
                    commonService.revokeRole(payload);
                }
            }

            commonService.updateUser(vm.user.user)
                .then(function (response) {
                    if (!response.status || response.status === 200) {
                        $uibModalInstance.close(response);
                    } else {
                        errorMessage(response);
                        //                            $uibModalInstance.dismiss('An error occurred');
                    }
                },function (error) {
                    errorMessage(error.data.error);
                    //                        $uibModalInstance.dismiss(error.data.error);
                });
        }

        function cancel () {
            $uibModalInstance.dismiss('cancelled');
        }

        function deleteUser () {
            var userObject;
            if (vm.acbId) {
                userObject = {acbId: vm.acbId,
                                  userId: vm.user.user.userId};
                commonService.removeUserFromAcb(userObject.userId, userObject.acbId)
                    .then(function (response) {
                        if (!response.status || response.status === 200) {
                            $uibModalInstance.close('deleted');
                        } else {
                            errorMessage(response);
                            //                                $uibModalInstance.dismiss('An error occurred');
                        }
                    },function (error) {
                        errorMessage(error.data.error);
                        //                            $uibModalInstance.dismiss(error.data.error);
                    });
            } else if (vm.atlId) {
                userObject = {atlId: vm.atlId,
                                  userId: vm.user.user.userId};
                commonService.removeUserFromAtl(userObject.userId, userObject.atlId)
                    .then(function (response) {
                        if (!response.status || response.status === 200) {
                            $uibModalInstance.close('deleted');
                        } else {
                            errorMessage(response);
                            //                                $uibModalInstance.dismiss('An error occurred');
                        }
                    },function (error) {
                        errorMessage(error.data.error);
                        //                            $uibModalInstance.dismiss(error.data.error);
                    });
            } else {
                commonService.deleteUser(vm.user.user.userId)
                    .then(function (response) {
                        if (!response.status || response.status === 200) {
                            $uibModalInstance.close('deleted');
                        } else {
                            errorMessage(response);
                            //                              $uibModalInstance.dismiss('An error occurred');
                        }
                    },function (error) {
                        errorMessage(error.data.error);
                        //                            $uibModalInstance.dismiss(error.data.error);
                    });
            }
        }

        function invite () {
            if (vm.acbId) {
                vm.userInvitation.acbId = vm.acbId;
            }
            if (vm.atlId) {
                vm.userInvitation.testingLabId = vm.atlId;
            }
            if (vm.userInvitation.emailAddress && vm.userInvitation.emailAddress.length > 0 && vm.userInvitation.permissions.length > 0) {
                commonService.inviteUser(vm.userInvitation)
                    .then(function (response) {
                        if (!response.status || response.status === 200) {
                            $uibModalInstance.close('invited');
                        } else {
                            errorMessage(response);
                            //                                $uibModalInstance.dismiss('An error occurred');
                        }
                    },function (error) {
                        errorMessage(error.data.error);
                        //                            $uibModalInstance.dismiss(error.data.error);
                    });
            }
        }
        ////////////////////////////////////////////////////////////////////

        function errorMessage (text) {
            vm.message = 'An error occurred. Please try again or contact the administrator. The error was: "' + text + '"';
        }
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('SurveillanceInspectController', SurveillanceInspectController);

    /** @ngInject */
    function SurveillanceInspectController ($uibModalInstance, $uibModal, $log, surveillance, commonService, utilService) {
        var vm = this;

        vm.cancel = cancel;
        vm.confirm = confirm;
        vm.editSurveillance = editSurveillance;
        vm.inspectNonconformities = inspectNonconformities;
        vm.reject = reject;
        vm.sortRequirements = utilService.sortRequirements;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.surveillance = angular.copy(surveillance);
            vm.errorMessages = [];
            vm.surveillanceTypes = commonService.getSurveillanceLookups();
        }

        function cancel () {
            $uibModalInstance.dismiss('cancelled');
        }

        function confirm () {
            commonService.confirmPendingSurveillance(vm.surveillance)
                .then(function () {
                    $uibModalInstance.close({status: 'confirmed'});
                }, function (error) {
                    if (error.data.messages) {
                        vm.errorMessages = error.data.errorMessages;
                    } else {
                        vm.errorMessages = [error.statusText];
                    }
                });
        }

        function editSurveillance () {
            fixRequirementOptions();
            vm.editModalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/surveillance/editSurveillance.html',
                controller: 'EditSurveillanceController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                size: 'lg',
                resolve: {
                    surveillance: function () { return vm.surveillance; },
                    surveillanceTypes: function () { return vm.surveillanceTypes; },
                    workType: function () { return 'confirm'; }
                }
            });
            vm.editModalInstance.result.then(function (result) {
                vm.surveillance = result;
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug('dismissed', result);
                }
            });
        }

        function inspectNonconformities (noncons) {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/surveillance/nonconformityInspect.html',
                controller: 'NonconformityInspectController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    nonconformities: function () { return noncons; }
                },
                size: 'lg'
            });
            vm.modalInstance.result.then(function (response) {
                noncons = response;
            }, function (result) {
                $log.info(result);
            });
        }

        function reject () {
            commonService.rejectPendingSurveillance(vm.surveillance.id)
                .then(function () {
                    $uibModalInstance.dismiss('rejected');
                },function (error) {
                    vm.errorMessages = [error.statusText]
                });
        }

        ////////////////////////////////////////////////////////////////////

        function fixRequirementOptions () {
            if (vm.surveillance.certifiedProduct.edition === '2015') {
                vm.surveillanceTypes.surveillanceRequirements.criteriaOptions = vm.surveillanceTypes.surveillanceRequirements.criteriaOptions2015;
            } else if (vm.surveillance.certifiedProduct.edition === '2014') {
                vm.surveillanceTypes.surveillanceRequirements.criteriaOptions = vm.surveillanceTypes.surveillanceRequirements.criteriaOptions2014;
            }
        }
    }
})();

