(function () {
    'use strict';

    angular.module('chpl.admin')
        .directive('aiLogin', function () {
            return {
                restrict: 'E',
                replace: true,
                templateUrl: 'app/admin/components/login/login.html',
                scope: {
                },
                bindToController: {
                    formClass: '@',
                    pClass: '@',
                    pClassFail: '@'
                },
                controllerAs: 'vm',
                controller: 'LoginController'
            };
        })
        .controller('LoginController', LoginController);

    /** @ngInclude */
    function LoginController ($scope, commonService, authService, $log, Idle, Keepalive) {
        var vm = this;

        vm.activate = activate;
        vm.changePassword = changePassword;
        vm.clear = clear;
        vm.isAuthed = isAuthed;
        vm.login = login;
        vm.logout = logout;
        vm.misMatchPasswords = misMatchPasswords;
        vm.sendReset = sendReset;
        vm.setActivity = setActivity;
        vm.pwPattern = "(?=.*\\d)(?=.*[a-z])(?=.*[A-Z])(?=.*\\W).{8,}";

        vm.activityEnum = {
            LOGIN: 1,
            CHANGE: 2,
            RESET: 3,
            NONE: 4
        };

        vm.activate();

        /////////////////////////////////////////////////////////

        function activate () {
            if (vm.isAuthed()) {
                vm.activity = vm.activityEnum.NONE;
            } else {
                vm.activity = vm.activityEnum.LOGIN;
            }
            vm.clear();
            $scope.$on('Keepalive', function() {
                $log.info('Keepalive');

                if (authService.isAuthed()) {
                    if (vm.activity === vm.activityEnum.RESET || vm.activity === vm.activityEnum.LOGIN) {
                        vm.activity = vm.activityEnum.NONE;
                    }
                    commonService.keepalive()
                        .then(function (response) {
                            authService.saveToken(response.token);
                        });
                } else {
                    vm.activity = vm.activityEnum.LOGIN;
                    Idle.unwatch();
                }
            });
            if (authService.isAuthed()) {
                Idle.watch();
            }
        }

        function changePassword () {
            if (vm.newPassword === vm.confirmPassword) {
                commonService.changePassword({oldPassword: vm.password, newPassword: vm.newPassword})
                    .then(function () {
                        vm.clear();
                        vm.messageClass = vm.pClass;
                        vm.message = 'Password successfully changed';
                    }, function () {
                        vm.messageClass = vm.pClassFail;
                        vm.message = 'Error. Please check your credentials or contact the administrator';
                    });
            } else {
                vm.message = 'Passwords do not match. Please try again';
            }
        }

        function clear () {
            if (vm.isAuthed()) {
                vm.activity = vm.activityEnum.NONE;
            } else {
                vm.activity = vm.activityEnum.LOGIN;
            }
            vm.userName = '';
            vm.password = '';
            vm.newPassword = '';
            vm.confirmPassword = '';
            vm.email = '';
            vm.message = '';
            if (vm.loginForm) {
                vm.loginForm.$setPristine();
                vm.loginForm.$setUntouched();
            }
        }

        function isAuthed () {
            return authService.isAuthed();
        }

        function login () {
            vm.message = '';
            commonService.login({userName: vm.userName, password: vm.password})
                .then(function () {
                    Idle.watch();
                    Keepalive.ping();
                    vm.clear();
                }, function (error) {
                    vm.messageClass = vm.pClassFail;
                    vm.message = error.data.error;
                });
        }

        function logout () {
            authService.logout();
            vm.clear();
            Idle.unwatch();
        }

        function setActivity (activity) {
            vm.activity = activity;
        }

        function misMatchPasswords () {
            return vm.newPassword !== vm.confirmPassword;
        }

        function sendReset () {
            commonService.resetPassword({userName: vm.userName, email: vm.email})
                .then(function () {
                    vm.clear();
                    vm.messageClass = vm.pClass;
                    vm.message = 'Password email sent; please check your email';
                }, function () {
                    vm.messageClass = vm.pClassFail;
                    vm.message = 'Invalid username/email combination. Please check your credentials or contact the administrator';
                });
        }
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('MergeVersionController', MergeVersionController);

    /** @ngInject */
    function MergeVersionController ($uibModalInstance, versions, productId, commonService) {
        var vm = this;

        vm.save = save;
        vm.cancel = cancel;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.versions = angular.copy(versions);
            vm.version = angular.copy(vm.versions[0]);
            delete vm.version.lastModifiedDate;
            delete vm.version.versionId;
            vm.updateVersion = {versionIds: [],
                                newProductId: productId};
            for (var i = 0; i < vm.versions.length; i++) {
                vm.updateVersion.versionIds.push(vm.versions[i].versionId);
            }
        }

        function save () {
            vm.updateVersion.version = vm.version;
            commonService.updateVersion(vm.updateVersion)
                .then(function (response) {
                    if (!response.status || response.status === 200) {
                        $uibModalInstance.close(response);
                    } else {
                        $uibModalInstance.dismiss('An error occurred');
                    }
                },function (error) {
                    $uibModalInstance.dismiss(error.data.error);
                });
        }

        function cancel () {
            $uibModalInstance.dismiss('cancelled');
        }
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('MergeProductController', MergeProductController);

    /** @ngInject */
    function MergeProductController ($uibModalInstance, products, developerId, commonService) {
        var vm = this;

        vm.addPreviousOwner = addPreviousOwner;
        vm.removePreviousOwner = removePreviousOwner;
        vm.save = save;
        vm.cancel = cancel;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            commonService.getDevelopers(true).then(function (developers) {
                vm.developers = developers.developers;
            });
            vm.products = angular.copy(products);
            vm.product = angular.copy(vm.products[0]);
            delete vm.product.lastModifiedDate;
            delete vm.product.productId;
            vm.product.ownerHistory = [];
            vm.updateProduct = {
                productIds: [],
                newDeveloperId: developerId
            };
            for (var i = 0; i < vm.products.length; i++) {
                vm.updateProduct.productIds.push(vm.products[i].productId);
            }
        }

        function addPreviousOwner () {
            vm.product.ownerHistory.push({});
        }

        function removePreviousOwner (idx) {
            vm.product.ownerHistory.splice(idx, 1);
        }

        function save () {
            vm.updateProduct.product = vm.product;
            for (var i = 0; i < vm.product.ownerHistory.length; i++) {
                vm.product.ownerHistory[i].transferDate = vm.product.ownerHistory[i].transferDate.getTime();
            }
            commonService.updateProduct(vm.updateProduct)
                .then(function (response) {
                    if (!response.status || response.status === 200) {
                        $uibModalInstance.close(response);
                    } else {
                        $uibModalInstance.dismiss('An error occurred');
                    }
                },function (error) {
                    $uibModalInstance.dismiss(error.data.error);
                });
        }

        function cancel () {
            $uibModalInstance.dismiss('cancelled');
        }
    }
})();

