(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('VpManagementController', VpManagementController)
        .directive('aiVpManagement', function () {
            return {
                restrict: 'E',
                replace: true,
                templateUrl: 'app/admin/components/cp/vpManagement.html',
                bindToController: {
                    workType: '=?',
                    pendingProducts: '=?',
                    pendingSurveillances: '=?',
                    productId: '='
                },
                scope: {},
                controllerAs: 'vm',
                controller: 'VpManagementController'
            };
        });

    /** @ngInject */
    function VpManagementController (commonService, authService, $log, FileUploader, API, $uibModal) {
        var vm = this;

        vm.doWork = doWork;
        vm.editCertifiedProduct = editCertifiedProduct;
        vm.editDeveloper = editDeveloper;
        vm.editProduct = editProduct;
        vm.editVersion = editVersion;
        vm.inspectCp = inspectCp;
        vm.inspectSurveillance = inspectSurveillance;
        vm.isDeveloperEditable = isDeveloperEditable;
        vm.isDeveloperMergeable = isDeveloperMergeable;
        vm.isProductEditable = isProductEditable;
        vm.loadCp = loadCp;
        vm.loadSurveillance = loadSurveillance;
        vm.mergeDevelopers = mergeDevelopers;
        vm.mergeProducts = mergeProducts;
        vm.mergeVersions = mergeVersions;
        vm.parseUploadError = parseUploadError;
        vm.parseSurveillanceUploadError = parseSurveillanceUploadError;
        vm.refreshDevelopers = refreshDevelopers;
        vm.refreshPending = refreshPending;
        vm.rejectCp = rejectCp;
        vm.rejectSurveillance = rejectSurveillance;
        vm.searchForSurveillance = searchForSurveillance;
        vm.selectCp = selectCp;
        vm.selectDeveloper = selectDeveloper;
        vm.selectProduct = selectProduct;
        vm.selectVersion = selectVersion;
        vm.ternaryFilter = ternaryFilter;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.activeDeveloper = '';
            vm.activeProduct = '';
            vm.activeVersion = '';
            vm.activeCP = '';
            vm.isChplAdmin = authService.isChplAdmin();
            vm.isAcbAdmin = authService.isAcbAdmin();
            vm.isAcbStaff = authService.isAcbStaff();
            vm.uploadingCps = [];
            vm.uploadingSurveillances = [];
            if (angular.isUndefined(vm.workType)) vm.workType = 'manage';//vm.workType = vm.productId ? 'manage' : vm.isChplAdmin ? 'manage' : 'upload';
            vm.mergeType = 'developer';
            vm.uploadMessage = '';
            vm.uploadErrors = [];
            vm.uploadSuccess = true;
            vm.surveillanceUploadMessage = '';
            vm.surveillanceUploadErrors = [];
            vm.surveillanceUploadSuccess = true;
            vm.resources = {};
            vm.refreshDevelopers();

            if (vm.isAcbAdmin || vm.isAcbStaff) {
                vm.refreshPending();
                vm.uploader = new FileUploader({
                    url: API + '/certified_products/upload',
                    removeAfterUpload: true,
                    headers: {
                        Authorization: 'Bearer ' + authService.getToken(),
                        'API-Key': authService.getApiKey()
                    }
                });
                if (angular.isUndefined(vm.uploader.filters)) {
                    vm.uploader.filters = [];
                }
                vm.uploader.filters.push({
                    name: 'csvFilter',
                    fn: function(item) {
                        var extension = '|' + item.name.slice(item.name.lastIndexOf('.') + 1) + '|';
                        return '|csv|'.indexOf(extension) !== -1;
                    }
                });
                vm.uploader.onSuccessItem = function(fileItem, response, status, headers) {
                    $log.info('onSuccessItem', fileItem, response, status, headers);
                    vm.uploadMessage = 'File "' + fileItem.file.name + '" was uploaded successfully. ' + response.pendingCertifiedProducts.length + ' pending products are ready for confirmation.';
                    vm.uploadErrors = [];
                    vm.uploadSuccess = true;
                };
                vm.uploader.onCompleteItem = function(fileItem, response, status, headers) {
                    $log.info('onCompleteItem', fileItem, response, status, headers);
                    vm.refreshPending();
                };
                vm.uploader.onErrorItem = function(fileItem, response, status, headers) {
                    $log.info('onErrorItem', fileItem, response, status, headers);
                    vm.uploadMessage = 'File "' + fileItem.file.name + '" was not uploaded successfully.';
                    vm.uploadErrors = response.errorMessages;
                    vm.uploadSuccess = false;
                };
                vm.uploader.onCancelItem = function(fileItem, response, status, headers) {
                    $log.info('onCancelItem', fileItem, response, status, headers);
                };

                vm.surveillanceUploader = new FileUploader({
                    url: API + '/surveillance/upload',
                    removeAfterUpload: true,
                    headers: {
                        Authorization: 'Bearer ' + authService.getToken(),
                        'API-Key': authService.getApiKey()
                    }
                });
                if (angular.isUndefined(vm.surveillanceUploader.filters)) {
                    vm.surveillanceUploader.filters = [];
                }
                vm.surveillanceUploader.filters.push({
                    name: 'csvFilter',
                    fn: function(item) { //, options) {
                        var extension = '|' + item.name.slice(item.name.lastIndexOf('.') + 1) + '|';
                        return '|csv|'.indexOf(extension) !== -1;
                    }
                });
                vm.surveillanceUploader.onSuccessItem = function(fileItem, response, status, headers) {
                    $log.info('onSuccessItem', fileItem, response, status, headers);
                    vm.surveillanceUploadMessage = 'File "' + fileItem.file.name + '" was uploaded successfully. ' + response.pendingSurveillance.length + ' pending surveillance records are ready for confirmation.';
                    vm.surveillanceUploadErrors = [];
                    vm.surveillanceUploadSuccess = true;
                };
                vm.surveillanceUploader.onCompleteItem = function(fileItem, response, status, headers) {
                    $log.info('onCompleteItem', fileItem, response, status, headers);
                    vm.refreshPending();
                };
                vm.surveillanceUploader.onErrorItem = function(fileItem, response, status, headers) {
                    $log.info('onErrorItem', fileItem, response, status, headers);
                    vm.surveillanceUploadMessage = 'File "' + fileItem.file.name + '" was not uploaded successfully.';
                    vm.surveillanceUploadErrors = response.errorMessages;
                    vm.surveillanceUploadSuccess = false;
                };
                vm.surveillanceUploader.onCancelItem = function(fileItem, response, status, headers) {
                    $log.info('onCancelItem', fileItem, response, status, headers);
                };
            }

            getResources();
        }

        function refreshDevelopers () {
            commonService.getDevelopers()
                .then(function (developers) {
                    vm.developers = developers.developers;
                    prepCodes();

                    if (vm.productId && vm.workType === 'manage') {
                        vm.loadCp();
                    } else if (vm.productId && vm.workType === 'manageSurveillance') {
                        vm.loadSurveillance();
                    }
                });
        }

        function refreshPending () {
            commonService.getUploadingCps ()
                .then(function (cps) {
                    vm.uploadingCps = [].concat(cps.pendingCertifiedProducts);
                    vm.pendingProducts = vm.uploadingCps.length;
                })
            commonService.getUploadingSurveillances ()
                .then(function (surveillances) {
                    vm.uploadingSurveillances = [].concat(surveillances.pendingSurveillance);
                    vm.pendingSurveillances = vm.uploadingSurveillances.length;
                })
        }

        function selectDeveloper () {
            if (vm.developerSelect) {
                vm.activeDeveloper = vm.developerSelect;
                commonService.getProductsByDeveloper(vm.activeDeveloper.developerId)
                    .then(function (products) {
                        vm.products = products.products;
                    });
                vm.mergeDeveloper = angular.copy(vm.activeDeveloper);
                delete vm.mergeDeveloper.developerId;
                delete vm.mergeDeveloper.lastModifiedDate;
            }
        }

        function editDeveloper () {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/cp/vpEditDeveloper.html',
                controller: 'EditDeveloperController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    activeDeveloper: function () { return vm.activeDeveloper; },
                    activeAcbs: function () { return vm.activeAcbs; }
                }
            });
            vm.modalInstance.result.then(function (result) {
                vm.activeDeveloper = result;
                commonService.getDevelopers()
                    .then(function (developers) {
                        vm.developers = developers.developers;
                        prepCodes();
                    });
            }, function (result) {
                if (result !== 'cancelled') {
                    vm.developerMessage = result;
                }
            });
        }

        function mergeDevelopers () {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/cp/vpMergeDeveloper.html',
                controller: 'MergeDeveloperController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                size: 'lg',
                resolve: {
                    developers: function () { return vm.mergingDevelopers; }
                }
            });
            vm.modalInstance.result.then(function () {
                vm.developerMessage = null;
                commonService.getDevelopers()
                    .then(function (developers) {
                        vm.developers = developers.developers;
                        prepCodes();
                    });
            }, function (result) {
                if (result !== 'cancelled') {
                    vm.developerMessage = result;
                }
            });
        }

        function selectProduct () {
            if (vm.productSelect) {
                vm.activeProduct = vm.productSelect;
                vm.activeProduct.developerId = vm.activeDeveloper.developerId;
                commonService.getVersionsByProduct(vm.activeProduct.productId)
                    .then(function (versions) {
                        vm.versions = versions;
                    });
                vm.mergeProduct = angular.copy(vm.activeProduct);
                delete vm.mergeProduct.productId;
                delete vm.mergeProduct.lastModifiedDate;
            }
        }

        function editProduct () {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/cp/vpEditProduct.html',
                controller: 'EditProductController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    activeProduct: function () { return vm.activeProduct; }
                }
            });
            vm.modalInstance.result.then(function (result) {
                vm.activeProduct = result;
            }, function (result) {
                if (result !== 'cancelled') {
                    vm.productMessage = result;
                }
            });
        }

        function mergeProducts () {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/cp/vpMergeProduct.html',
                controller: 'MergeProductController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                size: 'lg',
                resolve: {
                    products: function () { return vm.mergingProducts; },
                    developerId: function () { return vm.activeDeveloper.developerId; }
                }
            });
            vm.modalInstance.result.then(function () {
                vm.productMessage = null;
                commonService.getProductsByDeveloper(vm.activeDeveloper.developerId)
                    .then(function (products) {
                        vm.products = products.products;
                    });
            }, function (result) {
                if (result !== 'cancelled') {
                    vm.productMessage = result;
                }
            });
        }

        function mergeVersions () {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/cp/vpMergeVersion.html',
                controller: 'MergeVersionController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    versions: function () { return vm.mergingVersions; },
                    productId: function () { return vm.activeProduct.productId; }
                }
            });
            vm.modalInstance.result.then(function () {
                vm.productMessage = null;
                commonService.getVersionsByProduct(vm.activeProduct.productId)
                    .then(function (versions) {
                        vm.versions = versions;
                    });
            }, function (result) {
                if (result !== 'cancelled') {
                    vm.productMessage = result;
                }
            });
        }

        function selectVersion () {
            if (vm.versionSelect) {
                vm.activeVersion = vm.versionSelect;
                vm.activeVersion.productId = vm.activeProduct.productId;
                commonService.getProductsByVersion(vm.activeVersion.versionId, true)
                    .then(function (cps) {
                        vm.cps = cps;
                    });
                vm.mergeVersion = angular.copy(vm.activeVersion);
                delete vm.mergeVersion.versionId;
                delete vm.mergeVersion.lastModifiedDate;
            }
        }

        function editVersion () {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/cp/vpEditVersion.html',
                controller: 'EditVersionController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    activeVersion: function () { return vm.activeVersion; }
                }
            });
            vm.modalInstance.result.then(function (result) {
                vm.activeVersion = result;
            }, function (result) {
                if (result !== 'cancelled') {
                    vm.versionMessage = result;
                }
            });
        }

        function selectCp () {
            if (vm.cpSelect) {
                vm.activeCP = {};
                vm.activeCP.certifyingBody = {};
                vm.activeCP.practiceType = {};
                vm.activeCP.classificationType = {};
                commonService.getProduct(vm.cpSelect)
                    .then(function (cp) {
                        vm.activeCP = cp;
                        vm.activeCP.certDate = new Date(vm.activeCP.certificationDate);
                        commonService.getCap(vm.cpSelect)
                            .then(function (cap) {
                                vm.activeCP.cap = cap.plans;
                            });
                    })
            }
        }

        function editCertifiedProduct () {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/cp/vpEditCertifiedProduct.html',
                controller: 'EditCertifiedProductController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                size: 'lg',
                resolve: {
                    activeCP: function () { return vm.activeCP; },
                    isAcbAdmin: function () { return vm.isAcbAdmin; },
                    isAcbStaff: function () { return vm.isChplStaff; },
                    isChplAdmin: function () { return vm.isChplAdmin; },
                    resources: function () { return vm.resources; },
                    workType: function () { return vm.workType; }
                }
            });
            vm.modalInstance.result.then(function (result) {
                vm.activeCP = result;
                getResources();
                vm.productId = result.id;
                vm.refreshDevelopers();
                vm.loadCp();
            }, function (result) {
                if (result !== 'cancelled') {
                    vm.cpMessage = result;
                }
            });
        }

        function inspectCp (cpId) {
            var cp;
            for (var i = 0; i < vm.uploadingCps.length; i++) {
                if (cpId === vm.uploadingCps[i].id) {
                    cp = vm.uploadingCps[i];
                }
            }

            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/cp/vpInspect.html',
                controller: 'InspectController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    developers: function () { return vm.developers; },
                    inspectingCp: function () { return cp; },
                    isAcbAdmin: function () { return vm.isAcbAdmin; },
                    isAcbStaff: function () { return vm.isAcbStaff; },
                    isChplAdmin: function () { return vm.isChplAdmin; },
                    resources: function () { return vm.resources; },
                    workType: function () { return vm.workType; }
                },
                size: 'lg'
            });
            vm.modalInstance.result.then(function (result) {
                if (result.developerCreated) {
                    vm.developers.push(result.developer);
                }
                for (var i = 0; i < vm.uploadingCps.length; i++) {
                    if (cpId === vm.uploadingCps[i].id) {
                        vm.uploadingCps.splice(i,1)
                        vm.pendingProducts = vm.uploadingCps.length;
                    }
                }
            }, function (result) {
                if (result !== 'cancelled') {
                    if (result.developerCreated) {
                        vm.developers.push(result.developer);
                    }
                    for (var i = 0; i < vm.uploadingCps.length; i++) {
                        if (cpId === vm.uploadingCps[i].id) {
                            vm.uploadingCps.splice(i,1)
                            vm.pendingProducts = vm.uploadingCps.length;
                        }
                    }
                }
            });
        }

        function inspectSurveillance (surv) {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/surveillance/surveillanceInspect.html',
                controller: 'SurveillanceInspectController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    surveillance: function () { return surv; }
                },
                size: 'lg'
            });
            vm.modalInstance.result.then(function () {
                vm.refreshPending();
            }, function (result) {
                if (result !== 'cancelled') {
                    vm.refreshPending();
                }
            });
        }

        function isDeveloperEditable (dev) {
            return vm.isChplAdmin || dev.status.status === 'Active';
        }

        function isDeveloperMergeable (dev) {
            return dev.status.status === 'Active';
        }

        function isProductEditable (cp) {
            if (cp.certificationStatus) {
                return (vm.isChplAdmin || (cp.certificationStatus.name !== 'Suspended by ONC' && cp.certificationStatus.name !== 'Terminated by ONC')) &&
                    vm.isDeveloperMergeable(vm.activeDeveloper);
            } else {
                return vm.isDeveloperMergeable(vm.activeDeveloper);
            }
        }

        function rejectCp (cpId) {
            commonService.rejectPendingCp(cpId)
                .then(vm.refreshPending);
        }

        function rejectSurveillance (survId) {
            commonService.rejectPendingSurveillance(survId)
                .then(vm.refreshPending);
        }

        function searchForSurveillance () {
            var query = {
                pageNumber: 0,
                pageSize: '50',
                searchTerm: vm.surveillanceSearch.query
            };
            vm.surveillanceProduct = null;
            vm.surveillanceSearch.results = null;
            commonService.search(query)
                .then(function (response) {
                    vm.surveillanceSearch.results = response.results;
                    if (vm.surveillanceSearch.results.length === 1) {
                        vm.productId = vm.surveillanceSearch.results[0].id;
                        vm.loadSurveillance();
                    }
                });
        }

        function parseUploadError (cp) {
            var ret = '';
            if (cp.recordStatus.toLowerCase() !== 'new') {
                ret = 'Existing Certified Product found';
            } else {
                if (cp.errorMessages.length > 0) {
                    ret += 'Errors:&nbsp;' + cp.errorMessages.length;
                }
                if (cp.warningMessages.length > 0) {
                    if (ret.length > 0)
                        ret += '<br />';
                    ret += 'Warnings:&nbsp;' + cp.warningMessages.length;
                }
                if (ret.length === 0) {
                    ret = 'OK';

                }
            }
            return ret;
        }

        function parseSurveillanceUploadError (surv) {
            var ret = '';
            if (surv.errorMessages.length > 0) {
                ret += 'Errors:&nbsp;' + surv.errorMessages.length;
            }
            if (surv.warningMessages && surv.warningMessages.length > 0) {
                if (ret.length > 0)
                    ret += '<br />';
                ret += 'Warnings:&nbsp;' + surv.warningMessages.length;
            }
            if (ret.length === 0) {
                ret = 'OK';
            }
            return ret;
        }

        function doWork (workType) {
            if (vm.workType !== workType) {
                vm.activeDeveloper = '';
                vm.activeProduct = '';
                vm.activeVersion = '';
                vm.activeCP = '';
                vm.mergeType = 'developer';
                vm.workType = workType;
            }
        }

        function loadCp () {
            commonService.getProduct(vm.productId)
                .then(function (result) {
                    for (var i = 0; i < vm.developers.length; i++) {
                        if (result.developer.developerId === vm.developers[i].developerId) {
                            vm.developerSelect = vm.developers[i];
                            break;
                        }
                    }
                    vm.activeDeveloper = vm.developerSelect;
                    commonService.getProductsByDeveloper(vm.activeDeveloper.developerId)
                        .then(function (products) {
                            vm.products = products.products;
                            for (var i = 0; i < vm.products.length; i++) {
                                if (result.product.productId === vm.products[i].productId) {
                                    vm.productSelect = vm.products[i];
                                    break;
                                }
                            }
                            vm.activeProduct = vm.productSelect;
                            vm.activeProduct.developerId = vm.activeDeveloper.developerId;
                            commonService.getVersionsByProduct(vm.activeProduct.productId)
                                .then(function (versions) {
                                    vm.versions = versions;
                                    for (var i = 0; i < vm.versions.length; i++) {
                                        if (result.version.versionId === vm.versions[i].versionId) {
                                            vm.versionSelect = vm.versions[i];
                                            break;
                                        }
                                    }
                                    vm.activeVersion = vm.versionSelect;
                                    vm.activeVersion.productId = vm.activeProduct.productId;
                                    commonService.getProductsByVersion(vm.activeVersion.versionId, true)
                                        .then(function (cps) {
                                            vm.cps = cps;
                                            vm.cpSelect = result.id;
                                            vm.selectCp();
                                        });
                                });
                        });
                });
        }

        function loadSurveillance () {
            commonService.getProduct(vm.productId)
                .then(function (result) {
                    vm.surveillanceProduct = result;
                });
        }

        function ternaryFilter (field) {
            if (field == null) {
                return 'N/A';
            } else {
                return field ? 'True' : 'False';
            }
        }

        ////////////////////////////////////////////////////////////////////

        function getResources () {
            commonService.getSearchOptions()
                .then(function (options) {
                    vm.resources.bodies = options.certBodyNames;
                    vm.resources.classifications = options.productClassifications;
                    vm.resources.editions = options.editions;
                    vm.resources.practices = options.practiceTypeNames;
                    vm.resources.statuses = options.certificationStatuses;
                });

            commonService.getAtls(false)
                .then(function (data) {
                    vm.resources.testingLabs = data.atls;
                });

            commonService.getQmsStandards()
                .then(function (response) {
                    vm.resources.qmsStandards = response;
                });

            commonService.getAccessibilityStandards()
                .then(function (response) {
                    vm.resources.accessibilityStandards = response;
                });

            commonService.getTestStandards()
                .then(function (response) {
                    vm.resources.testStandards = response;
                });

            commonService.getUcdProcesses()
                .then(function (response) {
                    vm.resources.ucdProcesses = response;
                });

            commonService.getTestFunctionality()
                .then(function (response) {
                    vm.resources.testFunctionalities = response;
                });

            commonService.getTestTools()
                .then(function (response) {
                    vm.resources.testTools = response;
                });

            commonService.getTargetedUsers()
                .then(function (response) {
                    vm.resources.targetedUsers = response;
                });
        }

        function prepCodes () {
            var values = {};
            for (var i = 0; i < vm.developers.length; i++) {
                vm.developers[i].transMap = {};
                for (var j = 0; j < vm.developers[i].transparencyAttestations.length; j++) {
                    vm.developers[i].transMap[vm.developers[i].transparencyAttestations[j].acbName] = vm.developers[i].transparencyAttestations[j].attestation;
                    values[vm.developers[i].transparencyAttestations[j].acbName] = true;
                }
            }
            vm.activeAcbs = [];
            angular.forEach(values, function (value, key) {
                vm.activeAcbs.push(key);
            });
        }
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('InspectController', InspectController);

    /** @ngInject */
    function InspectController ($uibModalInstance, $uibModal, $log, inspectingCp, developers, isAcbAdmin, isAcbStaff, isChplAdmin, resources, commonService) {
        var vm = this;

        vm.loadDev = loadDev;
        vm.selectInspectingDeveloper = selectInspectingDeveloper;
        vm.saveInspectingDeveloper = saveInspectingDeveloper;

        vm.loadPrd = loadPrd;
        vm.selectInspectingProduct = selectInspectingProduct;
        vm.saveInspectingProduct = saveInspectingProduct;

        vm.loadVer = loadVer;
        vm.selectInspectingVersion = selectInspectingVersion;
        vm.saveInspectingVersion = saveInspectingVersion;

        vm.confirm = confirm;
        vm.reject = reject;
        vm.editCertifiedProduct = editCertifiedProduct;

        vm.next = next;
        vm.previous = previous;
        vm.isDisabled = isDisabled;

        vm.cancel = cancel;
        vm.ternaryFilter = ternaryFilter;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.cp = angular.copy(inspectingCp);
            vm.stage = 'dev';

            vm.developers = developers;
            vm.developerChoice = 'choose';
            vm.loadDev();

            vm.products = [];
            vm.productChoice = 'choose';

            vm.versions = [];
            vm.versionChoice = 'choose';

            vm.errorMessages = [];
            vm.isAcbAdmin = isAcbAdmin;
            vm.isAcbStaff = isAcbStaff;
            vm.isChplAdmin = isChplAdmin;
            vm.resources = resources;
            vm.statuses = resources.statuses;
            for (var i = 0; i < vm.statuses.length; i++) {
                if (vm.statuses[i].name === 'Pending') {
                    vm.cp.certificationStatus = vm.statuses[i];
                    break;
                }
            }
            if (!vm.cp.developer.country) {
                vm.cp.developer.country = 'USA';
            }
        }

        function loadDev () {
            if (vm.cp.developer.developerId) {
                commonService.getDeveloper(vm.cp.developer.developerId)
                    .then(function (result) {
                        vm.developer = result;
                    });
            }
        }

        function selectInspectingDeveloper() {
            vm.cp.developer.developerId = vm.developerSelect.developerId;
            vm.loadDev();
        }

        function saveInspectingDeveloper() {
            var dev = {
                developer: {
                    name: vm.cp.developer.name,
                    status: vm.developer.status,
                    website: vm.cp.developer.website,
                    address: vm.cp.developer.address,
                    transparencyAttestations: [{acbId: vm.cp.certifyingBody.id, acbName: vm.cp.certifyingBody.name, attestation: vm.cp.transparencyAttestation}],
                    contact: vm.cp.developer.contact,
                    developerId: vm.cp.developer.developerId
                },
                developerIds: [vm.cp.developer.developerId]
            };
            if (!dev.developer.address.country) {
                dev.developer.address.country = 'USA';
            }
            commonService.updateDeveloper(dev)
                .then(function () {
                    vm.loadDev();
                });
        }

        function loadPrd () {
            if (vm.developer && vm.developer.developerId) {
                commonService.getProductsByDeveloper(vm.developer.developerId)
                    .then(function (result) {
                        vm.products = result.products;
                    });
            } else {
                vm.productChoice = 'create';
            }
            if (vm.cp.product.id) {
                commonService.getSimpleProduct(vm.cp.product.id)
                    .then(function (result) {
                        vm.product = result;
                    });
            }
        }

        function selectInspectingProduct() {
            vm.cp.product.id = vm.productSelect.productId;
            vm.loadPrd();
        }

        function saveInspectingProduct() {
            var prd = {
                product: {
                    name: vm.cp.product.name,
                    productId: vm.cp.product.id
                },
                productIds: [vm.cp.product.id],
                newDeveloperId: vm.cp.developer.developerId
            };
            commonService.updateProduct(prd)
                .then(function () {
                    vm.loadPrd();
                });
        }

        function loadVer () {
            if (vm.product && vm.product.productId) {
                commonService.getVersionsByProduct(vm.product.productId)
                    .then(function (result) {
                        vm.versions = result;
                    });
            } else {
                vm.versionChoice = 'create';
            }
            if (vm.cp.version.versionId) {
                commonService.getVersion(vm.cp.version.versionId)
                    .then(function (result) {
                        vm.version = result;
                    });
            }
        }

        function selectInspectingVersion() {
            vm.cp.version.versionId = vm.versionSelect.versionId;
            vm.loadVer();
        }

        function saveInspectingVersion() {
            var ver = {
                version: {
                    version: vm.cp.version.version,
                    productId: vm.cp.version.versionId
                },
                versionIds: [vm.cp.version.versionId]
            };
            commonService.updateVersion(ver)
                .then(function () {
                    vm.loadVer();
                });
        }

        function confirm () {
            commonService.confirmPendingCp(vm.cp)
                .then(function (result) {
                    $uibModalInstance.close({status: 'confirmed', developerCreated: vm.developerChoice === 'create', developer: result.developer});
                }, function (error) {
                    vm.errorMessages = error.data.errorMessages;
                });
        }

        function reject () {
            commonService.rejectPendingCp(vm.cp.id)
                .then(function () {
                    $uibModalInstance.dismiss('rejected');
                });
        }

        function editCertifiedProduct () {
            vm.editModalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/cp/vpEditCertifiedProduct.html',
                controller: 'EditCertifiedProductController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                size: 'lg',
                resolve: {
                    activeCP: function () { return vm.cp; },
                    isAcbAdmin: function () { return vm.isAcbAdmin; },
                    isAcbStaff: function () { return vm.isAcbStaff; },
                    isChplAdmin: function () { return vm.isChplAdmin; },
                    resources: function () { return vm.resources; },
                    workType: function () { return 'confirm'; }
                }
            });
            vm.editModalInstance.result.then(function (result) {
                vm.cp = result;
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug('dismissed', result);
                }
            });
        }

        function next () {
            switch (vm.stage) {
            case 'dev':
                vm.stage = 'prd';
                vm.loadPrd();
                break;
            case 'prd':
                vm.stage = 'ver';
                vm.loadVer();
                break;
            case 'ver':
                vm.stage = 'cp';
                break;
            default:
                break;
            }
        }

        function previous () {
            switch (vm.stage) {
            case 'prd': vm.stage = 'dev';
                break;
            case 'ver': vm.stage = 'prd';
                break;
            case 'cp': vm.stage = 'ver';
                break;
            default:
                break;
            }
        }

        function isDisabled () {
            switch (vm.stage) {
            case 'dev':
                if (vm.developerChoice === 'choose' && !vm.cp.developer.developerId)
                    return true;
                return false;
            case 'prd':
                if (vm.productChoice === 'choose' && !vm.cp.product.id)
                    return true;
                return false;
            case 'ver':
                if (vm.versionChoice === 'choose' && !vm.cp.version.versionId)
                    return true;
                return false;
            default:
                return true;
            }
        }

        function cancel () {
            $uibModalInstance.dismiss('cancelled');
        }

        function ternaryFilter (field) {
            if (field == null) {
                return 'N/A';
            } else {
                return field ? 'True' : 'False';
            }
        }
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('EditVersionController', EditVersionController);

    /** @ngInject */
    function EditVersionController ($uibModalInstance, activeVersion, commonService) {
        var vm = this;
        vm.version = angular.copy(activeVersion);
        vm.updateVersion = {versionIds: [vm.version.versionId]};

        vm.save = save;
        vm.cancel = cancel;

        ////////////////////////////////////////////////////////////////////

        function save () {
            vm.updateVersion.version = vm.version;
            commonService.updateVersion(vm.updateVersion)
                .then(function (response) {
                    if (!response.status || response.status === 200) {
                        response.Id = vm.version.productId;
                        $uibModalInstance.close(response);
                    } else {
                        $uibModalInstance.dismiss('An error occurred');
                    }
                },function (error) {
                    $uibModalInstance.dismiss(error.data.error);
                });
        }

        function cancel () {
            $uibModalInstance.dismiss('cancelled');
        }
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('EditProductController', EditProductController);

    /** @ngInject */
    function EditProductController ($uibModalInstance, activeProduct, commonService) {
        var vm = this;
        vm.product = angular.copy(activeProduct);
        vm.updateProduct = {productIds: [vm.product.productId]};

        vm.addPreviousOwner = addPreviousOwner;
        vm.changeCurrent = changeCurrent;
        vm.removePreviousOwner = removePreviousOwner;
        vm.save = save;
        vm.cancel = cancel;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            commonService.getDevelopers(true).then(function (developers) {
                vm.developers = developers.developers;
            });
            for (var i = 0; i < vm.product.ownerHistory.length; i++) {
                vm.product.ownerHistory[i].transferDate = new Date(vm.product.ownerHistory[i].transferDate);
            }
        }

        function addPreviousOwner () {
            vm.product.ownerHistory.push({});
        }

        function changeCurrent (prevId) {
            vm.product.ownerHistory.push({
                developer: {
                    developerId: prevId
                },
                transferDate: new Date()
            });
        }

        function removePreviousOwner (idx) {
            vm.product.ownerHistory.splice(idx, 1);
        }

        function save () {
            for (var i = 0; i < vm.product.ownerHistory.length; i++) {
                vm.product.ownerHistory[i].transferDate = vm.product.ownerHistory[i].transferDate.getTime();
            }
            vm.updateProduct.product = vm.product;
            vm.updateProduct.newDeveloperId = vm.product.developerId;
            commonService.updateProduct(vm.updateProduct)
                .then(function (response) {
                    if (!response.status || response.status === 200) {
                        response.developerId = vm.product.developerId;
                        $uibModalInstance.close(response);
                    } else {
                        $uibModalInstance.dismiss('An error occurred');
                    }
                },function (error) {
                    $uibModalInstance.dismiss(error.data.error);
                });
        }

        function cancel () {
            $uibModalInstance.dismiss('cancelled');
        }
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('EditDeveloperController', EditDeveloperController);

    /** @ngInject */
    function EditDeveloperController ($uibModalInstance, activeDeveloper, activeAcbs, commonService, authService) {
        var vm = this;
        vm.developer = angular.copy(activeDeveloper);
        vm.updateDeveloper = {developerIds: [vm.developer.developerId]};
        vm.activeAcbs = angular.copy(activeAcbs);

        vm.addressRequired = addressRequired;
        vm.isBeingActivatedFromOncInactiveStatus = isBeingActivatedFromOncInactiveStatus;
        vm.save = save;
        vm.cancel = cancel;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.isAcbAdmin = authService.isAcbAdmin();
            vm.isChplAdmin = authService.isChplAdmin();
            vm.showFormErrors = false;
            vm.loadedAsInactiveByOnc = (vm.developer.status.status === 'Suspended by ONC' || vm.developer.status.status === 'Under certification ban by ONC');
        }

        function addressRequired () {
            return commonService.addressRequired(vm.developer.address);
        }

        function isBeingActivatedFromOncInactiveStatus () {
            return vm.loadedAsInactiveByOnc && vm.developer.status.status !== 'Suspended by ONC' && vm.developer.status.status !== 'Under certification ban by ONC';
        }

        function save () {
            vm.updateDeveloper.developer = vm.developer;
            angular.forEach(vm.developer.transMap, function (value, key) {
                var found = false;
                for (var i = 0; i < vm.developer.transparencyAttestations.length; i++) {
                    if (vm.developer.transparencyAttestations[i].acbName === key) {
                        vm.developer.transparencyAttestations[i].attestation = value;
                        found = true;
                    }
                }
                if (!found) {
                    vm.developer.transparencyAttestations.push({acbName: key, attestation: value});
                }
            });
            commonService.updateDeveloper(vm.updateDeveloper)
                .then(function (response) {
                    if (!response.status || response.status === 200 || angular.isObject(response.status)) {
                        $uibModalInstance.close(response);
                    } else {
                        $uibModalInstance.dismiss('An error occurred');
                    }
                },function (error) {
                    $uibModalInstance.dismiss(error.data.error);
                });
        }

        function cancel () {
            $uibModalInstance.dismiss('cancelled');
        }
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('EditCertifiedProductController', EditCertifiedProductController);

    /** @ngInject */
    function EditCertifiedProductController ($uibModalInstance, $timeout, activeCP, commonService, utilService, isAcbAdmin, isAcbStaff, isChplAdmin, resources, workType) {

        var vm = this;
        vm.addNewValue = addNewValue;
        vm.attachModel = attachModel;
        vm.cancel = cancel;
        vm.directCertsDirective = directCertsDirective;
        vm.disabledStatus = disabledStatus;
        vm.extendSelect = extendSelect;
        vm.findModel = findModel;
        vm.prep = prep;
        vm.registerCerts = registerCerts;
        vm.save = save;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.cp = angular.copy(activeCP);
            vm.cp.certDate = new Date(vm.cp.certificationDate);
            vm.cp.sedTestingEndDate = new Date(vm.cp.sedTestingEnd);
            vm.isAcbAdmin = isAcbAdmin;
            vm.isAcbStaff = isAcbStaff;
            vm.isChplAdmin = isChplAdmin;
            vm.bodies = resources.bodies;
            vm.classifications = resources.classifications;
            vm.practices = resources.practices;
            vm.qmsStandards = resources.qmsStandards;
            vm.accessibilityStandards = resources.accessibilityStandards;
            vm.targetedUsers = resources.targetedUsers;
            vm.statuses = resources.statuses;
            vm.testingLabs = resources.testingLabs;
            vm.resources = resources;
            vm.workType = workType;
            vm.showFormErrors = false;
            vm.message = '';
            if (vm.cp.chplProductNumber.length > 12) {
                var idFields = vm.cp.chplProductNumber.split('.');
                vm.idFields = {
                    prefix: idFields[0] + '.' + idFields[1] + '.' + idFields[2] + '.' + idFields[3],
                    prod: idFields[4],
                    ver: idFields[5],
                    ics: idFields[6],
                    suffix: idFields[7] + '.' + idFields[8]
                };
            }

            vm.handlers = [];
            vm.attachModel();
        }

        function addNewValue (array, object) {
            if (!array) {
                array = [];
            }
            if (object && object !== {}) {
                array.push(angular.copy(object));
            }
        }

        function attachModel () {
            vm.cp.practiceType = vm.findModel(vm.cp.practiceType, vm.practices);
            vm.cp.classificationType = vm.findModel(vm.cp.classificationType, vm.classifications);
            vm.cp.certifyingBody = vm.findModel(vm.cp.certifyingBody, vm.bodies);
            if (vm.cp.testingLab) {
                vm.cp.testingLab = vm.findModel(vm.cp.testingLab, vm.testingLabs);
            }
            vm.cp.certificationStatus = vm.findModel(vm.cp.certificationStatus, vm.statuses);
        }

        function cancel () {
            $uibModalInstance.dismiss('cancelled');
        }

        function directCertsDirective () {
            angular.forEach(vm.handlers, function (handler) {
                handler();
            });
        }

        function disabledStatus (name) {
            return ((name === 'Pending' && vm.workType === 'manage') || (name !== 'Pending' && vm.workType === 'confirm'));
        }

        function extendSelect (options, value) {
            options = utilService.extendSelect(options, value);
        }

        function findModel (id, array) {
            for (var i = 0; i < array.length; i++) {
                if (id.id === array[i].id) {
                    id = array[i];
                }
            }
            return id;
        }

        function prep () {
            vm.directCertsDirective();
            $timeout(vm.save, 1000);
        }

        function registerCerts (handler) {
            vm.handlers.push(handler);
            var removeHandler = function () {
                vm.handlers = vm.handlers.filter(function (aHandler) {
                    return aHandler !== handler;
                });
            };
            return removeHandler;
        }

        function save () {
            if (vm.cp.chplProductNumber.length > 12) {
                vm.cp.chplProductNumber =
                    vm.idFields.prefix + '.' +
                    vm.idFields.prod + '.' +
                    vm.idFields.ver + '.' +
                    vm.idFields.ics + '.' +
                    vm.idFields.suffix;
            }
            vm.cp.certificationDate = vm.cp.certDate.getTime();
            vm.cp.sedTestingEnd = vm.cp.sedTestingEndDate.getTime();
            if (vm.workType === 'manage') {
                commonService.updateCP(vm.cp)
                    .then(function (response) {
                        if (!response.status || response.status === 200) {
                            $uibModalInstance.close(response);
                        } else {
                            vm.errors = [response.error];
                        }
                    },function (error) {
                        vm.errors = [];
                        if (error.data) {
                            if (error.data.error && error.data.error.length > 0)
                                vm.errors.push(error.data.error);
                            if (error.data.errorMessages && error.data.errorMessages.length > 0)
                                vm.errors = vm.errors.concat(error.data.errorMessages);
                            if (error.data.warningMessage && error.data.warningMessage.length > 0)
                                vm.errors = vm.errors.concat(error.data.warningMessage);
                        }
                    });
            } else if (vm.workType === 'confirm') {
                $uibModalInstance.close(vm.cp);
            }
        }
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('CmsController', CmsController)
        .directive('aiCmsManagement', function () {
            return {
                restrict: 'E',
                replace: true,
                templateUrl: 'app/admin/components/cms/cms.html',
                scope: {},
                bindToController: {
                    //admin: '='
                },
                controllerAs: 'vm',
                controller: 'CmsController'
            };
        });

    /** @ngInject */
    function CmsController ($log, commonService, authService, FileUploader, API) {
        var vm = this;

        vm.getDownload = getDownload;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.isAcbAdmin = authService.isAcbAdmin();
            vm.isOncStaff = authService.isOncStaff();
            vm.isChplAdmin = authService.isChplAdmin();

            vm.uploader = new FileUploader({
                url: API + '/certified_products/meaningful_use_users/upload',
                removeAfterUpload: true,
                headers: {
                    Authorization: 'Bearer ' + authService.getToken(),
                    'API-Key': authService.getApiKey()
                }
            });

            if (angular.isUndefined(vm.uploader.filters)) {
                vm.uploader.filters = [];
            }
            vm.uploader.filters.push({
                name: 'csvFilter',
                fn: function(item) {
                    var extension = '|' + item.name.slice(item.name.lastIndexOf('.') + 1) + '|';
                    return '|csv|'.indexOf(extension) !== -1;
                }
            });
            vm.uploader.onSuccessItem = function(fileItem, response, status, headers) {
                $log.info('onSuccessItem', fileItem, response, status, headers);
                vm.uploadMessage = 'File "' + fileItem.file.name + '" was uploaded successfully. ' + response.meaningfulUseUsers.length + ' certified products out of ' + (response.errors.length + response.meaningfulUseUsers.length) + ' were updated with meaningful use user counts.';
                vm.uploadErrors = response.errors;
                vm.uploadSuccess = true;
            };
            vm.uploader.onCompleteItem = function(fileItem, response, status, headers) {
                $log.info('onCompleteItem', fileItem, response, status, headers);
            };
            vm.uploader.onErrorItem = function(fileItem, response, status, headers) {
                $log.info('onErrorItem', fileItem, response, status, headers);
                vm.uploadMessage = 'File "' + fileItem.file.name + '" was not uploaded successfully.';
                vm.uploadErrors = response.errorMessages;
                vm.uploadSuccess = false;
            };
            vm.uploader.onCancelItem = function(fileItem, response, status, headers) {
                $log.info('onCancelItem', fileItem, response, status, headers);
            };
            vm.filename = 'CMS_IDs_' + new Date().getTime() + '.csv';
            if (authService.isChplAdmin() || authService.isOncStaff()) {
                vm.csvHeader = ['CMS ID', 'Creation Date', 'CHPL Product(s)'];
                vm.csvColumnOrder = ['certificationId', 'created', 'products'];
            } else {
                vm.csvHeader = ['CMS ID', 'Creation Date'];
                vm.csvColumnOrder = ['certificationId', 'created'];
            }
            vm.isReady = false;
            vm.getDownload();
        }

        function getDownload () {
            commonService.getCmsDownload()
                .then(function (result) {
                    for (var i = 0; i < result.length; i++) {
                        result[i].created = new Date(result[i].created).toISOString().substring(0, 10);
                    }
                    vm.cmsArray = result
                    vm.isReady = true;
                }, function (error) {
                    $log.debug('error in app.admin.cmsController.getDownload', error);
                });
        }
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('AtlManagementController', AtlManagementController)
        .directive('aiAtlManagement', aiAtlManagement);

    /** @ngInject */
    function aiAtlManagement () {
        return {
            restrict: 'E',
            replace: true,
            templateUrl: 'app/admin/components/atl/atlManagement.html',
            bindToController: {
                workType: '=?',
                activeAtl: '=?'
            },
            scope: {},
            controllerAs: 'vm',
            controller: 'AtlManagementController'
        };
    }

    /** @ngInject */
    function AtlManagementController (commonService, authService, $log, $uibModal) {
        var vm = this;

        vm.doWork = doWork;
        vm.activate = activate;
        vm.activateAtl = activateAtl;
        vm.loadData = loadData;
        vm.createAtl = createAtl;
        vm.editAtl = editAtl;

        vm.activate();

        ////////////////////////////////////////////////////////////////////

        function doWork (workType) {
            if (workType === 'newAtl') {
                vm.activeAtl = null;
            }
            vm.workType = workType;
        }

        function activate () {
            vm.isChplAdmin = authService.isChplAdmin();
            vm.isAtlAdmin = authService.isAtlAdmin();
            vm.atls = [];
            vm.workType = 'atl';
        }

        function activateAtl (atl) {
            vm.workType = 'atl';
            vm.activeAtl = atl;
        }

        function loadData () {
            return commonService.getAtls()
                .then (function (data) {
                    vm.atls = data.atls;
                });
        }

        function createAtl () {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/atlEdit.html',
                controller: 'EditAtlController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    atl: function () { return {}; },
                    action: function () { return 'create'; },
                    isChplAdmin: function () { return vm.isChplAdmin; }
                }
            });
            vm.modalInstance.result.then(function () {
                vm.activate();
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug(result);
                }
            });
        }

        function editAtl (atl) {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/atlEdit.html',
                controller: 'EditAtlController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    atl: function () { return atl; },
                    action: function () { return 'edit'; },
                    isChplAdmin: function () { return vm.isChplAdmin; }
                }
            });
            vm.modalInstance.result.then(function (result) {
                if (result !== 'deleted') {
                    vm.activeAtl = result;
                } else {
                    vm.activate();
                }
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug(result);
                }
            });
        }

        vm.cancelATL = function() {
            vm.loadData();
        };

        vm.deleteATL = function (atl) {
            commonService.deleteATL(atl.id)
                .then(function () {
                    vm.activate();
                });
        };

        vm.addressRequired = function (atl) {
            if (atl)
                return commonService.addressRequired(atl.address);
        };
    }
})();

