(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('AdditionalSoftwareController', AdditionalSoftwareController)
        .directive('aiAdditionalSoftware', function () {
            return {
                restrict: 'E',
                replace: true,
                templateUrl: 'app/admin/components/additionalSoftware/additionalSoftware.html',
                bindToController: {
                    additionalSoftware: '=?',
                    isEditing: '=?'
                },
                scope: {},
                controllerAs: 'vm',
                controller: 'AdditionalSoftwareController'
            };
        });

    /** @ngInject */
    function AdditionalSoftwareController ($log, $scope, $uibModal) {
        var vm = this;

        vm.addItem = addItem;
        vm.editItem = editItem;
        vm.isAndOrOr = isAndOrOr;
        vm.removeItem = removeItem;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.displaySw = {};
            vm.groupCount = 0;
            if (vm.additionalSoftware) {
                for (var i = 0; i < vm.additionalSoftware.length; i++) {
                    if (vm.additionalSoftware[i].grouping === null) {
                        vm.displaySw['defaultGroup' + i] = [vm.additionalSoftware[i]];
                        vm.groupCount += 1;
                    } else {
                        if (!vm.displaySw[vm.additionalSoftware[i].grouping]) {
                            vm.displaySw[vm.additionalSoftware[i].grouping] = [];
                            vm.groupCount += 1;
                        }
                        vm.displaySw[vm.additionalSoftware[i].grouping].push(vm.additionalSoftware[i]);
                    }
                }
            }
        }

        function addItem () {
            vm.editModalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/additionalSoftware/additionalSoftwareModal.html',
                controller: 'EditAdditionalSoftwareController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    software: function () { return { name: '', version: '', certifiedProductSelfCHPLId: '' }; }
                }
            });
            vm.editModalInstance.result.then(function (result) {
                if (!vm.additionalSoftware) {
                    vm.additionalSoftware = [];
                }
                vm.additionalSoftware.push(result);
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug('dismissed', result);
                }
            });
        }

        function editItem (sw, index) {
            vm.editModalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/additionalSoftware/additionalSoftwareModal.html',
                controller: 'EditAdditionalSoftwareController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    software: function () { return sw; }
                }
            });
            vm.editModalInstance.result.then(function (result) {
                vm.additionalSoftware[index] = result;
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug('dismissed', result);
                }
            });
        }

        function isAndOrOr (index, groupLength, parentIndex, groupCount) {
            if (index < (groupLength - 1)) {
                return 'OR';
            } else if (parentIndex < (groupCount - 1)) {
                return 'AND';
            }
            return '';
        }

        function removeItem (index) {
            vm.additionalSoftware.splice(index, 1);
        }
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('AcbManagementController', AcbManagementController)
        .directive('aiAcbManagement', aiAcbManagement);

    /** @ngInject */
    function aiAcbManagement () {
        return {
            restrict: 'E',
            replace: true,
            templateUrl: 'app/admin/components/acb/acbManagement.html',
            bindToController: {
                workType: '=?',
                activeAcb: '=?'
            },
            scope: {},
            controllerAs: 'vm',
            controller: 'AcbManagementController'
        };
    }

    /** @ngInject */
    function AcbManagementController (commonService, authService, $log, $uibModal) {
        var vm = this;

        vm.doWork = doWork;
        vm.activateAcb = activateAcb;
        vm.loadData = loadData;
        vm.createAcb = createAcb;
        vm.editAcb = editAcb;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.isChplAdmin = authService.isChplAdmin();
            vm.isAcbAdmin = authService.isAcbAdmin();
            vm.acbs = [];
            vm.workType = 'acb';
        }

        function doWork (workType) {
            if (workType === 'newAcb') {
                vm.activeAcb = null;
            }
            vm.workType = workType;
        }

        function activateAcb (acb) {
            vm.workType = 'acb';
            vm.activeAcb = acb;
        }

        function loadData () {
            return commonService.getAcbs()
                .then (function (data) {
                    vm.acbs = data.acbs;
                });
        }

        function createAcb () {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/acb/acbEdit.html',
                controller: 'EditAcbController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    acb: function () { return {}; },
                    action: function () { return 'create'; },
                    isChplAdmin: function () { return vm.isChplAdmin; }
                }
            });
            vm.modalInstance.result.then(function () {
                vm.activate();
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug(result);
                }
            });
        }

        function editAcb (acb) {
            vm.modalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/acb/acbEdit.html',
                controller: 'EditAcbController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                resolve: {
                    acb: function () { return acb; },
                    action: function () { return 'edit'; },
                    isChplAdmin: function () { return vm.isChplAdmin; }
                }
            });
            vm.modalInstance.result.then(function (result) {
                if (result !== 'deleted') {
                    vm.activeAcb = result;
                } else {
                    vm.activate();
                }
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug(result);
                }
            });
        }

        vm.cancelACB = function() {
            vm.loadData();
        };

        vm.deleteACB = function (acb) {
            commonService.deleteACB(acb.id)
                .then(function () {
                    vm.activate();
                });
        };

        vm.addressRequired = function (acb) {
            if (acb)
                return commonService.addressRequired(acb.address);
        };
    }
})();

(function () {
    'use strict';

    angular.module('chpl.admin')
        .controller('EditAcbController', EditAcbController);

    /** @ngInject */
    function EditAcbController ($uibModalInstance, acb, action, isChplAdmin, commonService) {
        var vm = this;
        vm.acb = angular.copy(acb);
        vm.action = action;
        vm.isChplAdmin = isChplAdmin;

        vm.save = save;
        vm.cancel = cancel;
        vm.create = create;
        vm.deleteAcb = deleteAcb;
        vm.undeleteAcb = undeleteAcb;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            if (vm.action === 'create') {
                vm.acb.address = {};
            }
        }

        function save () {
            commonService.modifyACB(vm.acb)
                .then(function (response) {
                    if (!response.status || response.status === 200) {
                        $uibModalInstance.close(response);
                    } else {
                        $uibModalInstance.dismiss('An error occurred');
                    }
                },function (error) {
                    $uibModalInstance.dismiss(error.data.error);
                });
        }

        function cancel () {
            $uibModalInstance.dismiss('cancelled');
        }

        function create () {
            commonService.createACB(vm.acb)
                .then(function (response) {
                    if (!response.status || response.status === 200) {
                        $uibModalInstance.close(response);
                    } else {
                        $uibModalInstance.dismiss('An error occurred');
                    }
                },function (error) {
                    $uibModalInstance.dismiss(error.data.error);
                });
        }

        function deleteAcb () {
            commonService.deleteACB(vm.acb.id)
                .then(function (response) {
                    if (!response.status || response.status === 200) {
                        $uibModalInstance.close('deleted');
                    } else {
                        $uibModalInstance.dismiss('An error occurred');
                    }
                },function (error) {
                    $uibModalInstance.dismiss(error.data.error);
                });
        }

        function undeleteAcb () {
            commonService.undeleteACB(vm.acb.id)
                .then(function (response) {
                    if (!response.status || response.status === 200) {
                        $uibModalInstance.close(response);
                    } else {
                        $uibModalInstance.dismiss('An error occurred');
                    }
                },function (error) {
                    $uibModalInstance.dismiss(error.data.error);
                });
        }
    }
})();

