(function () {
    'use strict';

    angular.module('chpl')
        .controller('SurveillanceController', SurveillanceController)
        .directive('aiSurveillance', aiSurveillance);

    function aiSurveillance () {
        return {
            restrict: 'E',
            replace: true,
            templateUrl: 'app/components/surveillance/surveillance.html',
            scope: {},
            bindToController: {
                allowEditing: '=',
                certifiedProduct: '='
            },
            size: 'lg',
            controllerAs: 'vm',
            controller: 'SurveillanceController'
        };
    }

    function SurveillanceController ($log, $scope, $uibModal, $filter, commonService, authService, utilService, API) {
        var vm = this;

        vm.editSurveillance = editSurveillance;
        vm.getTitle = getTitle;
        vm.initiateSurveillance = initiateSurveillance;
        vm.sortRequirements = utilService.sortRequirements;
        vm.sortResults = sortResults;
        vm.surveillanceResults = surveillanceResults;

        activate();

        ////////////////////////////////////////////////////////////////////

        function activate () {
            vm.API = API;
            vm.API_KEY = authService.getApiKey();
            vm.surveillanceTypes = commonService.getSurveillanceLookups();
        }

        function editSurveillance (surveillance) {
            fixRequirementOptions();
            vm.uibModalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/surveillance/editSurveillance.html',
                controller: 'EditSurveillanceController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                size: 'lg',
                resolve: {
                    surveillance: function () { return surveillance; },
                    surveillanceTypes: function () { return vm.surveillanceTypes; },
                    workType: function () { return 'edit'; }
                }
            });
            vm.uibModalInstance.result.then(function () {
                commonService.getProduct(vm.certifiedProduct.id)
                    .then(function (result) {
                        vm.certifiedProduct = result;
                    });
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug(result);
                }
            });
        }

        function getTitle (surv) {
            var closed = surv.endDate ? true : false;
            var title = '';
            if (surv.friendlyId) {
                title += surv.friendlyId + ': ';
            }
            title += closed ?
                'Closed Surveillance, Ended ' + $filter('date')(surv.endDate, 'mediumDate', 'UTC') + ': ' :
                'Open Surveillance, Began ' + $filter('date')(surv.startDate, 'mediumDate', 'UTC') + ': ';
            var nonconformityCount = 0;
            for (var i = 0; i < surv.requirements.length; i++) {
                for (var j = 0; j < surv.requirements[i].nonconformities.length; j++) {
                    if (surv.requirements[i].nonconformities[j].status.name === 'Open') {
                        nonconformityCount += 1;
                    }
                }
            }
            title += nonconformityCount;
            if (nonconformityCount !== 1) {
                title += ' Open Non-Conformities Found';
            } else {
                title += ' Open Non-Conformity Found';
            }
            return title;
        }

        function initiateSurveillance () {
            fixRequirementOptions();
            vm.uibModalInstance = $uibModal.open({
                templateUrl: 'app/admin/components/surveillance/editSurveillance.html',
                controller: 'EditSurveillanceController',
                controllerAs: 'vm',
                animation: false,
                backdrop: 'static',
                keyboard: false,
                size: 'lg',
                resolve: {
                    surveillance: function () { return { certifiedProduct: vm.certifiedProduct }; },
                    surveillanceTypes: function () { return vm.surveillanceTypes; },
                    workType: function () { return 'initiate'; }
                }
            });
            vm.uibModalInstance.result.then(function () {
                commonService.getProduct(vm.certifiedProduct.id)
                    .then(function (result) {
                        vm.certifiedProduct = result;
                    });
            }, function (result) {
                if (result !== 'cancelled') {
                    $log.debug(result);
                }
            });
        }

        function sortResults (result) {
            var req = result.substring(result.indexOf(' for ') + 5);
            return vm.sortRequirements(req);
        }

        function surveillanceResults (surv) {
            var results = [];
            for (var i = 0; i < surv.requirements.length; i++) {
                for (var j = 0; j < surv.requirements[i].nonconformities.length; j++) {
                    results.push(surv.requirements[i].nonconformities[j].status.name + ' Non-Conformity Found for ' + surv.requirements[i].requirement);
                }
            }
            if (results.length === 0) {
                results.push('No Non-Conformities Found');
            }
            return results;
        }

        ////////////////////////////////////////////////////////////////////

        function fixRequirementOptions () {
            if (vm.certifiedProduct.certificationEdition.name === '2015') {
                vm.surveillanceTypes.surveillanceRequirements.criteriaOptions = vm.surveillanceTypes.surveillanceRequirements.criteriaOptions2015;
            } else if (vm.certifiedProduct.certificationEdition.name === '2014') {
                vm.surveillanceTypes.surveillanceRequirements.criteriaOptions = vm.surveillanceTypes.surveillanceRequirements.criteriaOptions2014;
            }
        }
    }
})();

(function () {
    'use strict';
    angular.module('chpl')

        .directive('stSelectMultiple', function() {
            return {
                restrict: 'E',
                require: '^stTable',
                scope: {
                    collection: '=',
                    predicate: '@',
                    predicateExpression: '='
                },
                templateUrl: 'app/components/smart_table/stSelectMultiple.html',
                link: function(scope, element, attr, table) {
                    scope.dropdownLabel = '';
                    scope.filterChanged = filterChanged;

                    initialize();

                    function initialize() {
                        bindCollection(scope.collection);
                        scope.$watch('collection', function (newCollection) {
                            bindCollection(newCollection);
                        });
                    }

                    function getPredicate() {
                        var predicate = scope.predicate;
                        if (!predicate && scope.predicateExpression) {
                            predicate = scope.predicateExpression;
                        }
                        return predicate;
                    }

                    function getDropdownLabel() {
                        var allCount = scope.distinctItems.length;

                        var selected = getSelectedOptions();

                        if (allCount === selected.length || selected.length === 0) {
                            return 'All';
                        }

                        if (selected.length === 1) {
                            return selected[0];
                        }

                        return selected.length + ' items';
                    }

                    function getSelectedOptions() {
                        var selectedOptions = [];

                        angular.forEach(scope.distinctItems, function(item) {
                            if (item.selected) {
                                selectedOptions.push(item.value);
                            }
                        });

                        return selectedOptions;
                    }

                    function bindCollection(collection) {
                        var predicate = getPredicate();
                        var distinctItems = [];

                        angular.forEach(collection, function(item) {
                            var value = item[predicate];
                            fillDistinctItems(value, distinctItems);
                        });

                        distinctItems.sort(function(obj, other) {
                            if (obj.value > other.value) {
                                return 1;
                            } else if (obj.value < other.value) {
                                return -1;
                            }
                            return 0;
                        });

                        scope.distinctItems = distinctItems;

                        filterChanged();
                    }

                    function filterChanged() {
                        scope.dropdownLabel = getDropdownLabel();

                        var predicate = getPredicate();

                        var query = {
                            matchAny: {}
                        };

                        query.matchAny.items = getSelectedOptions();
                        var numberOfItems = query.matchAny.items.length;
                        if (numberOfItems === 0 || numberOfItems === scope.distinctItems.length) {
                            query.matchAny.all = true;
                        } else {
                            query.matchAny.all = false;
                        }

                        table.search(query, predicate);
                    }

                    function fillDistinctItems(values, distinctItems) {
                        if (!angular.isObject(values)) {
                            values = [values];
                        }
                        var value;
                        for (var i = 0; i < values.length; i++) {
                            value = values[i];
                            if (value && value.trim().length > 0 && !findItemWithValue(distinctItems, value)) {
                                distinctItems.push({
                                    value: value,
                                    selected: true
                                });
                            }
                        }
                    }

                    function findItemWithValue(collection, value) {
                        for (var i = 0; i < collection.length; i++) {
                            if (collection[i].value === value)
                                return true;
                        }
                        return false;
                    }
                }
            }
        })
})();

(function () {
    'use strict';
    angular.module('chpl')

        .directive('stSelectDistinct', function() {
            return {
                restrict: 'E',
                require: '^stTable',
                scope: {
                    collection: '=',
                    predicate: '@',
                    predicateExpression: '='
                },
                template: '<select ng-model="selectedOption" ng-change="optionChanged(selectedOption)" ng-options="opt for opt in distinctItems"></select>',
                link: function(scope, element, attr, table) {
                    var getPredicate = function() {
                        var predicate = scope.predicate;
                        if (!predicate && scope.predicateExpression) {
                            predicate = scope.predicateExpression;
                        }
                        return predicate;
                    }

                    scope.$watch('collection', function(newValue) {
                        var predicate = getPredicate();

                        if (newValue) {
                            var temp = [];
                            scope.distinctItems = ['All'];

                            angular.forEach(scope.collection, function(item) {
                                var value = item[predicate];

                                if (value && value.trim().length > 0 && temp.indexOf(value) === -1) {
                                    temp.push(value);
                                }
                            });
                            temp.sort();

                            scope.distinctItems = scope.distinctItems.concat(temp);
                            scope.selectedOption = scope.distinctItems[0];
                            scope.optionChanged(scope.selectedOption);
                        }
                    }, true);

                    scope.optionChanged = function(selectedOption) {
                        var predicate = getPredicate();

                        var query = {};

                        query.distinct = selectedOption;

                        if (query.distinct === 'All') {
                            query.distinct = '';
                        }

                        table.search(query, predicate);
                    };
                }
            }
        })
})();

(function () {
    'use strict';
    angular.module('chpl')

        .directive('stNumberRange', function () {
            return {
                restrict: 'E',
                require: '^stTable',
                scope: {
                    lower: '=',
                    higher: '='
                },
                templateUrl: 'app/components/smart_table/stNumberRange.html',
                link: function (scope, element, attr, table) {
                    var inputs = element.find('input');
                    var inputLower = angular.element(inputs[0]);
                    var inputHigher = angular.element(inputs[1]);
                    var predicateName = attr.predicate;

                    [inputLower, inputHigher].forEach(function (input) {

                        input.bind('blur', function () {
                            var query = {};

                            if (scope.lower) {
                                query.lower = scope.lower;
                            }

                            if (scope.higher) {
                                query.higher = scope.higher;
                            }

                            scope.$apply(function () {
                                table.search(query, predicateName)
                            });
                        });
                    });
                }
            };
        })
})();

