		//
		/////////////////////////////////////////////////////////////////////////////////////////////////////
		setCollectionChangeCallback: function (callback) {
			collectionChangeCallback = callback;
		},

		/////////////////////////////////////////////////////////////////////////////////////////////////////
		//
		/////////////////////////////////////////////////////////////////////////////////////////////////////
		generatePdf: function () {
			var certificationIdData = JSON.parse(chplCertIdWidget.getCertificationIdData());

			// Call API to attempt to get an EHR Certification ID
			$.ajax({
				url: urlCertId + certificationIdData.ehrCertificationId,
				type: "GET",
				cache: false,
				headers: {"API-KEY": apiKey},
				data: "includeCriteria=true",
				success: function(data, status, xhr) {
					chplCertIdWidget.generatePdf2(data);
				},
				error: function(xhr, status, error) {
					//alert("Error: " + error);
				}
			});
		},

		generatePdf2: function(data) {

			if (!data || "undefined" === data) {
				return;
			}

			// Setup the product listing
			var columns = ["", "Certifying Body", "Practice Type", "Product Certification #", "Developer",
				"Product Name", "Version", "Classification", "Certification Edition",
				"Additional Software Required"];
			var rows = [];
			var prods = data["products"];
			prods.forEach(function(item,index) {
				// Decode additional software
				var software = decodeURIComponent(item.additionalSoftware);
				if (null !== software) {
					software = software.replace(/\+/g, " ");
				}
				// Add product details row
				rows.push(
					[index + 1,
					item.acb,
					(null !== item.practiceType ? item.practiceType : "N/A"),
					item.chplProductNumber,
					item.vendor,
					item.name,
					item.version,
					(null !== item.classification ? item.classification : "N/A"),
					item.year,
					software]);
			});

			// Setup the criteria listing
			var certificationIdData = JSON.parse(chplCertIdWidget.getCertificationIdData());
			var checkImageData = [
				"data:image/png;base64,",
				"data:image/png;base64,"
			];
			var checkImages = [];
			var critCols = [
				{title: certificationIdData.year + " CMS EHR Base Criteria Met", dataKey: "description"}
			];

			var critRows = chplCertIdWidget.getPdfCriteria(certificationIdData.year);

			// Start the PDF document
			var doc = new jsPDF("l","pt");
			doc.setDrawColor(0, 26, 70);
			doc.setFillColor(0, 26, 70);
			doc.rect(0, 10, 845, 93, "F");
			doc.addImage("data:image/png;base64,",
				"PNG", 40, 10, 336, 74);
			var bodyStartY = 120;

			// Add body text to PDF
			doc.setFontSize(10);
			doc.setFontType("normal");
			doc.text(40, bodyStartY, doc.splitTextToSize("The CMS EHR Certification ID shown corresponds to the collection of products listed below. Submit this ID as part of the attestation process for the CMS EHR Incentive Programs.",
				775));
			doc.text(40, bodyStartY+30, doc.splitTextToSize("* Additional certification criteria may need to be added in order to meet submission requirements for Medicaid and Medicare programs.",
				775));

			// Add Certification ID to PDF
			doc.setFontSize(20);
			doc.setFontType("bold");
			doc.text(300, bodyStartY+70, data.ehrCertificationId);

			// Add products table to PDF
			doc.setFontSize(10);
			doc.setFontType("normal");
			doc.autoTable(columns, rows, {
				theme: "grid",
				headerStyles: {
					valign: "middle",
					halign: "center",
					overflow: "linebreak",
					columnWidth: "auto",
					fillColor: [0, 112, 201]
				},
				bodyStyles: {
					valign: "middle",
					halign: "left",
					overflow: "linebreak",
					columnWidth: "auto"
				},
				startY: bodyStartY+90,
				margin: 20,
				pageBreak: "auto",
				tableWidth: "auto"
			});

			// Add criteria table to PDF
			if (null !== critRows) {
				var fontSize = 10;
				doc.addPage();
				doc.setFontSize(fontSize);
				doc.setFontType("normal");
				doc.autoTable(critCols, critRows, {
					headerStyles: {
						valign: "middle",
						halign: "center",
						overflow: "linebreak",
						columnWidth: "auto",
						fillColor: [0, 112, 201]
					},
					bodyStyles: {
						valign: "middle",
						halign: "left",
						overflow: "linebreak",
						columnWidth: "auto"
					},
					margin: 20,
					pageBreak: "avoid",
					tableWidth: "auto",
					drawCell: function (cell, cellData) {
						if (cellData.column.dataKey === "description") {
							if (cellData.row.raw.key) {
								var met = chplCertIdWidget.checkCriterionIsMet(cellData.row.raw.key, data.criteria);
								cell.textPos.x += 32;
								var descriptionParts = cellData.row.raw.description.split("#");
								for (var partIndex=0; partIndex < descriptionParts.length; ++partIndex) {
									doc.text(descriptionParts[partIndex], cell.textPos.x, cell.textPos.y);
									cell.textPos.x += doc.getStringUnitWidth(descriptionParts[partIndex]) * fontSize;
									if (partIndex < met.length) {
										if (met[partIndex]) {
											checkImages.push({ elem: checkImageData[1], x: cell.textPos.x, y: cell.textPos.y - 8});
										} else {
											checkImages.push({ elem: checkImageData[0], x: cell.textPos.x, y: cell.textPos.y - 8});
										}
										cell.textPos.x += 18;
									}
								}
								return false;
							} else {
								checkImages.push(null);
							}

							return true;
						}
						return true;
					},
					afterPageContent: function() {
						var total = checkImages.length;
						for (var index=0; index < total; index++) {
							var img = checkImages.shift();
							if (null !== img) {
								doc.addImage(img.elem, 'png', img.x, img.y, 16, 10);
							}
						}
					}
				});
			} else {
				console.error("No PDF criteria layout for " + certificationIdData.year + "!");
			}

			// Output the PDF
			doc.save("" + new Date().getTime() + ".pdf");
		},

		checkCriterionIsMet: function(key, criteriaMet) {
			var keys = key.split(",");

			// Check for logic key values
			// 		| = (any criterion in list will do)
			// 		& = (all criteria in list must be met)
			if ("|" === keys[0]) {
				keys.shift();
				var result = [];
				for (var index=0; index < keys.length; ++index) {
					if (-1 !== criteriaMet.indexOf(keys[index])) {
						result.push(true);
					} else {
						result.push(false);
					}
				}
				return result;
			} else if ("&" === keys[0]) {
				keys.shift();
				var result = true;
				for (var index=0; index < keys.length; ++index) {
					if (-1 === criteriaMet.indexOf(keys[index]))
						result = false;
				}
				return [result];
			}
			if (keys.length > 1) {
				console.error("Multiple keys without a logic key value! (" + keys[0] + ") " + key + " -> " + keys);
			}
			return [(-1 !== criteriaMet.indexOf(keys[0]))];
		},

