(function () {
    'use strict';

    /** @ngInclude */
    function authInterceptor (API, authService) {
        return {
            // automatically attach Authorization header
            request: function (config) {
                var token = authService.getToken();
                var api_key = authService.getApiKey();
                if (config.url.indexOf(API) === 0) {
                    config.headers['API-Key'] = api_key;
                    if (token) {
                        config.headers.Authorization = 'Bearer ' + token;
                    }
                }

                return config;
            },

            // If a token was sent back, save it
            response: function (response) {
                if (response.config.url.indexOf(API) === 0) {
                    try {
                        if (angular.isString(response.data)) {
                            response.data = angular.fromJson(response.data);
                        }
                        if (response.data.token) {
                            authService.saveToken(response.data.token);
                        }
                    } catch (e) {
                        //console.log('data is not json', response.config.url, response.data, e);
                    }
                }
                return response;
            }
        }
    }

    angular.module('chpl.navigation', ['ngRoute', 'chpl.loginServices', 'chpl.common', 'chpl.constants'])
        .factory('authInterceptor', authInterceptor)
        .config(function($httpProvider) {
            $httpProvider.interceptors.push('authInterceptor');
        });
})();

